@import url(https://fonts.googleapis.com/css2?family=Baloo+Thambi+2&family=Megrim&display=swap);
.gem-container {
    height: 500px;
    width: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.gem {
    text-decoration: none;
    color:black;
    width:350px;
    height: 450px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.4);
    font-family: Baloo Thambi, sans-serif;
    grid-gap:5%;
    gap:5%;

    transition: all 0.2s ease 0s;


}
.gem:hover {
    width:370px;
    height: 475.7px;
}

.gem .images {
    width:90%;
    height: 70%;
}

.gem .gem-name {
    text-align: center;
    text-decoration: none;
    font-size: 100%;
    letter-spacing: 5px;
    

}

.gem .gem-cost {
    text-align: center;
    text-decoration: none;
    font-family: Baloo Thambi, sans-serif;
    font-size: 100%;
    letter-spacing: 5px;
}


/* descriptoin of item */

.item-desc-background {
    min-height:95vh;
    width:100vw;
    background-image: url(/gem-store/static/media/mainimage2.8f84efa9.jpg);
    background-repeat: no-repeat;
    background-size: cover;

    display: flex;
    align-items: center;
    justify-content: center;
}
.item-desc {
    width: 70%;
    min-height: 80vh;
    background-color: white;


    

    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 5%;
    gap: 5%;

    font-family: Baloo Thambi, sans-serif;
    letter-spacing: 5px;
    flex-wrap: wrap;
}

.item-image {
    width:700px;
    min-height: 700px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.4);

}

.item-desc-info {
    min-height: 700px;
    width: 400px;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: start;
    grid-gap: 10px;
    gap: 10px;
}
.item-name {
    font-size: 50px;
}

.item-cost {

}

.item-description {
    width:90%;
    letter-spacing: 3px;

}

.add-cart-button {
    padding:5px;
    align-self: center;
    margin-top: auto;
    transition: all 0.5s ease 0s;
    text-decoration: none;
    color:black;
    font-weight: 700;
    font-size: 18.75px;

}

.add-cart-button:hover {
    background-color: black;
    color:white;
}

.back-shop-button {
    padding: 5px;
    align-self: center;
    transition: all 0.5s ease 0s;
    text-decoration: none;
    color:black;
    font-weight: 700;
    font-size: 18.75px;
}
.back-shop-button:hover {
    background-color: black;
    color:white;

}
#shop {
    min-height:95vh;
    width:100vw;
    background-image: url(/gem-store/static/media/mainimage2.8f84efa9.jpg);
    background-repeat: no-repeat;
    background-size: cover;

    display: flex;
    align-items: center;
    justify-content: center;
}

#shop-platform {
    margin-top: 50px;
    margin-bottom: 50px;

    background-color: white;
    width: 80%;
    /* margin-top: ; */
    min-height: 80%; 
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.4);
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y:auto;
}

#shop-title {
    font-family: Baloo Thambi, sans-serif;
    font-size: 50px;
    letter-spacing: 10px;
    padding: 10px;
}

#shop-display{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    grid-gap: 40px;
    gap: 40px;
    text-decoration: none;
}

.shop-link {
    text-decoration: none;
}

.cart-background {
    min-height:95vh;
    width:100vw;
    background-image: url(/gem-store/static/media/mainimage2.8f84efa9.jpg);
    background-repeat: no-repeat;
    background-size: cover;

    display: flex;
    align-items: center;
    justify-content: center;
}

#cart {
    margin-top: 50px;
    margin-bottom: 50px;
    width: 70%;
    min-height: 80vh;
    background-color: white;


    

    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 5%;
    gap: 5%;

    font-family: Baloo Thambi, sans-serif;
    letter-spacing: 5px;
    flex-wrap: wrap;
}

.cart-title {
    font-family: Baloo Thambi, sans-serif;
    font-size: 50px;
    letter-spacing: 10px;
    padding: 10px;
}

.cart-items {

}

.cart-total {
    margin-top: auto;
}

.cart-payment-button {
    margin-top: 15px;
    padding: 5px;
    align-self: center;
    transition: all 0.5s ease 0s;
    text-decoration: none;
    color:black;
    font-weight: 700;
    font-size: 18.75px;
}

.cart-payment-button:hover {
    background-color: black;
    color:white;
}

.cart-shop-button {
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 5px;
    align-self: center;
    transition: all 0.5s ease 0s;
    text-decoration: none;
    color:black;
    font-weight: 700;
    font-size: 18.75px;
}

.cart-shop-button:hover {
    background-color: black;
    color:white;
}


/* the cart items */
.item {
    margin-bottom: 20px;
    margin-top: 10px;
    display: flex;
    grid-gap: 40px;
    gap: 40px;
}

.cart-item-image {
    width: 100px;
    height: 100px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.4);

}

.cart-desc {
    width:50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap:10px;
    gap:10px;
}

.cart-item-name {
    font-family: Baloo Thambi,sans-serif;
    font-weight: 400;
    letter-spacing: 3px;
    text-align: center;
}

.cart-item-cost {
    font-family: Baloo Thambi,sans-serif;
    font-weight: 400;
    letter-spacing: 3px;
    font-size: 20px;
}

.add-button {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    text-align: center;
}

.add-button button {
    border-style: none;
    border-radius: 50%;
    width:20px;
    height: 20px;
    background-color: rgb(73, 64, 64);
    color: white;
    font-family: 70px;
}

.add-button button:hover {
    opacity: .7;
}

.add-button p {
    width: 20px;
}



* {
    margin:0;
    padding:0;
}

/* nav bar stuff */
#nav {
    height: 5vh;
    width:100vw;
    display: flex;
    justify-content: end;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
    background-color: black;
}

.nav-link {
    text-decoration: none;
    color:white;
    padding:5px;
    font-family: Baloo Thambi, sans-serif;
    transition: all 0.5s ease 0s;
    letter-spacing: 3px;

}

.nav-link:hover {
    color:black;
    background-color: white;
    padding:5px;
}


#home-link {
    margin-right: auto;
    margin-left: 3vw;
    font-family: Megrim, sans-serif;
}

#cart-link {
    margin-right: 3vw;
}

/* main body stuff */
#home {
    min-height: 95vh; 
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    background-image: url(/gem-store/static/media/mainimage2.8f84efa9.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

#main-title {
    font-family: Megrim, sans-serif;
    color:white;
    padding:10px;
    background-color: black;
    font-size: 80px;
    margin-top: 20vh;
}

#main-quote {
    visibility: hidden;
    margin-top: 10px;
    font-family: Baloo Thambi, sans-serif;
    color:white;
    background-color: black;
}

#main-button {
    font-family: Baloo Thambi, sans-serif;
    letter-spacing: 10px;
    padding:10px;
    background-color: black;
    color:white;
    transition: all 0.5s ease 0s;

}

#main-button:hover {
    background-color: white;
    color:black;
}

#shop-link {
    margin-top: 30vh;
    text-decoration: none;
}

